#about-anchor,
#gallery-anchor,
#attivita-anchor,
#news-anchor,
#contatti-anchor {
  top: -200px;
  position: relative;
  visibility: hidden;
}
@keyframes orbit {
  from {
    transform: rotate(-134deg) translateX(125px) rotate(134deg);
  }
  to {
    transform: rotate(226deg) translateX(125px) rotate(-226deg);
  }
}
