.navbar {
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 90;
  width: 100%;
  border: 0;
  border-radius: 0;
  font-family: 'Sanchez';
}
.navbar .container {
  width: 80%;
}
.navbar h1.logo {
  text-indent: -9999px;
  background: url(../img/logo-stillpoint.png) no-repeat center center;
  width: 262px;
  height: 150px;
  margin: 20px 0;
  transition: 0.35s;
}
.navbar .navbar-right {
  margin-top: 74px;
  transition: 0.35s;
}
.navbar .navbar-right li:nth-child(5) a {
  margin-right: 0;
}
.navbar .navbar-right li.active {
  background: transparent;
  color: #439e7c;
}
.navbar .navbar-right li.active a {
  color: #439e7c;
  background: transparent;
}
.navbar .navbar-right li.active a:focus {
  background: transparent;
}
.navbar .navbar-right li.active a:after {
  position: absolute;
  content: '';
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 8px;
  height: 8px;
  background: #439e7c;
  border-radius: 50%;
}
.navbar .navbar-right li a {
  text-transform: uppercase;
  font-size: 18px;
  padding: 10px 25px;
  margin-right: 40px;
  color: #439e7c;
  position: relative;
}
.navbar .navbar-right li a:hover {
  color: #439e7c;
}
.navbar .navbar-right li a:hover:after {
  position: absolute;
  content: '';
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 8px;
  height: 8px;
  background: #439e7c;
  border-radius: 50%;
}
